import React, {Component} from 'react';
//   
import Total /*   ,    . export default*/ from './components/total/Total';
import {History} /* ,  , . export default*/ from './components/history/History';
import Operations /*   ,   , . export default*/ from './components/operation/Operation';
import SignIn from './Firebase/SignIn';

class App extends Component { //   

  state = {
    transactions: JSON.parse(localStorage.getItem("calcMoney")) || [],
    description: '',
    moneyAmount: '',
    totalIncome: 0, 
    totalExpenses: 0, 
    totalBalance: 0,
  }

  componentWillMount() { //componentWillMount      
    this.getTotalBalance();
  } 

  componentDidUpdate() {
    this.addToStorage(); // ,   ,     
  }

  //     Operation
  addTransaction = add => {
    const transactions = [...this.state.transactions]; //... - spread-

    transactions.push({ //  
      id: `cmr${(+new Date()).toString(16)}key`, //+  new Date       . 16     
      description: this.state.description,
      moneyAmount: this.state.moneyAmount,
      add
    });

    this.setState({ //   this?           (App)
      transactions, 
      description: '', 
      moneyAmount: '',
    }, this.getTotalBalance)
  }

  addAmount = e => { //  
    this.setState({moneyAmount: parseFloat(e.target.value)}) //   
  }

  addDescription = e => { //  
    this.setState({description: e.target.value}) 
  }


  getIncome() {
    return this.state.transactions
    .filter(item => item.add)
    .reduce((acc, item) => item.moneyAmount + acc, 0) 
    //reduce  ,  . 
    //0 -    
    // filter  ,  reduce  
  }

   //    (return  )    (  )
  getExpenses = () => this.state.transactions
  .reduce((acc, item) => !item.add ? item.moneyAmount + acc : acc, 0);

  getTotalBalance() {
    const totalIncome = this.getIncome();
    const totalExpenses = this.getExpenses();
    const totalBalance = totalIncome - totalExpenses;

    this.setState({
      totalIncome,
      totalExpenses,
      totalBalance,
    }, () => console.log(this.state));
  }


  //Local Storage
  addToStorage() {
    localStorage.setItem("calcMoney", JSON.stringify(this.state.transactions));
  }

  deleteTransaction = id => {
    //   ,           
    const transactions = this.state.transactions.filter(item => item.id !== id);
    this.setState({transactions}, this.getTotalBalance);
  }

  render() {
    return (
      /*     , .      React.Fragment*/
      <React.Fragment> 
        <header>
          <h1></h1>
          <h2> </h2>
        </header>
        <main>
            <div className="container">
                <SignIn/>
                <Total 
                  totalIncome = {this.state.totalIncome}
                  totalExpenses = {this.state.totalExpenses}
                  totalBalance = {this.state.totalBalance}
                />
                <History 
                  transactions = {this.state.transactions}
                  deleteTransaction = {this.deleteTransaction}
                />
                <Operations 
                  addTransaction={this.addTransaction}
                  addDescription={this.addDescription}
                  addAmount={this.addAmount}
                  description={this.state.description}
                  moneyAmount={this.state.moneyAmount}
                />
            </div>
        </main>
      </React.Fragment>
    );
  }
}

export default App;
