import React from 'react';

export default function Operation({addTransaction, addDescription, addAmount, description, moneyAmount}) { // props     
  return(
      <section className="operation">
      <h3> </h3>
      <form id="form">
          <label>
              <input 
                onChange={addDescription}
                type="text" 
                className="operation__fields operation__name" 
                placeholder=" "
                value={description}
              />
          </label>
          <label>
              <input onChange={addAmount} type="number" className="operation__fields operation__amount" value={moneyAmount} placeholder=" "/>
          </label>
          <div className="operation__btns">
              <button onClick={() => addTransaction(false)} 
                type="button" 
                className="operation__btn operation__btn-subtract"
                ></button>
              <button onClick={() => addTransaction(true)} 
              type="button" 
              className="operation__btn operation__btn-add"
              ></button>
          </div>
      </form>
  </section>
  )
}